import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { Link } from "gatsby"

const NotFoundPage = () => (
  <Layout>
    <Seo title="404: Not found" />
    <h1 style={{display: "block", textAlign: "center", paddingTop: 30}}>404: Not Found</h1>
    <p style={{display: "block", textAlign: "center", paddingBottom: 15}}>You just hit a route that doesn&#39;t exist... the sadness.</p>
    <Link to="/" style={{display: "block", textAlign: "center", paddingBottom: 30}}>Home</Link>
  </Layout>
)

export default NotFoundPage
